video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}

.section-container {
  /* background: url("img-1.jpg") center center/cover no-repeat; */
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
  justify-content: center;
}

/* Neon */

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #030c1c;
}

.main {
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: "neontubes";
  /* text-align: center; */
  white-space: nowrap;
}

.main span {
  display: block;
  position: relative;
  transform: translateZ(0) translate3D(0, 0, 0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  will-change: opacity;
}

.neon--head {
  font-size: 11vw;
  color: #ffd9e2;
  text-shadow: 0 0 0 transparent, 0 0 10px #d42450,
    0 0 20px rgba(173, 26, 60, 0.5), 0 0 40px #d42450, 0 0 100px #e4315e,
    0 0 200px #be2148, 0 0 300px #e2345f, 0 0 500px #d42450, 0 0 1000px #e7426b;
  -webkit-animation: blink 3s infinite alternate;
  animation: blink 3s infinite alternate;
  font-family: "neontubes";
}

.neon--subhead {
  font-size: 4vw;
  color: #324e68;
  text-shadow: 0 0 0 transparent, 0 0 5px #301d78,
    0 0 10px rgba(38, 149, 255, 0.5), 0 0 20px #301d78, 0 0 50px #301d78;
  -webkit-animation: buzz 0.01s infinite alternate;
  animation: buzz 0.01s infinite alternate;
  font-family: "neontubes";
  text-decoration: none;
  margin-right: 40px;
  transition: all 0.1s ease-in-out;
}

.main .neon--subhead:hover {
  color: #d4eaff;
  text-shadow: 0 0 0 transparent, 0 0 5px #3d2692,
    0 0 10px rgba(133, 190, 243, 0.5), 0 0 50px #6646d8, 0 0 100px #6244cf,
    0 0 100px #7a66c2, 0 0 500px #6046be, 0 0 1000px #634bbb;
  -webkit-animation: blink 3s infinite alternate;
  animation: blink 3s infinite alternate;
}

.neon--subhead2 {
  font-size: 3rem;
  color: #d4eaff;
  text-shadow: 0 0 0 transparent, 0 0 5px #3d2692,
    0 0 10px rgba(133, 190, 243, 0.5), 0 0 50px #6646d8, 0 0 100px #6244cf,
    0 0 100px #7a66c2, 0 0 500px #6046be, 0 0 1000px #634bbb;
  -webkit-animation: buzz 0.01s infinite alternate;
  animation: buzz 0.01s infinite alternate;
  font-family: "neontubes";
  text-decoration: none;
  margin-right: 40px;
  display: flex;
  justify-content: center;
}

.neon--random {
  font-size: 1.2rem;
  color: #d4eaff;
  text-shadow: 0 0 0 transparent, 0 0 5px #3d2692,
    0 0 5px rgba(133, 190, 243, 0.5), 0 0 6px #6646d8, 0 0 5px #6244cf,
    0 0 5px #7a66c2, 0 0 5px #6046be, 0 0 5px #634bbb;
  -webkit-animation: buzz 0.01s infinite alternate;
  animation: buzz 0.01s infinite alternate;
  font-family: "neontubes";
  text-decoration: none;
  margin-right: 40px;
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.contacts:hover {
  text-shadow: 0 0 0 transparent, 0 0 5px #3d2692,
    0 0 10px rgba(133, 190, 243, 0.5), 0 0 50px #6646d8, 0 0 100px #6244cf,
    0 0 100px #7a66c2, 0 0 500px #6046be, 0 0 1000px #634bbb;
  -webkit-animation: blink 3s infinite alternate;
          animation: blink 3s infinite alternate;
}
.random--wrapper {
  position: relative;
  margin-bottom: 0;
  padding-left: 4px;
  height: 50px;
  overflow: hidden;
}

.random--wrapper .neon--random {
  -webkit-animation: shuffle steps(8) 9s infinite;
          animation: shuffle steps(8) 9s infinite;
}

@-webkit-keyframes shuffle {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(-400px);
  }
  /* 10lines * 50 = 500px */
}

@keyframes shuffle {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(-400px);
  }
  /* 10lines * 50 = 500px */
}

@-webkit-keyframes buzz {
  70% {
    opacity: 0.8;
  }
}

@keyframes buzz {
  70% {
    opacity: 0.6;
  }
}
@-webkit-keyframes blink {
  40% {
    opacity: 1;
  }
  42% {
    opacity: 0.8;
  }
  43% {
    opacity: 1;
  }
  45% {
    opacity: 0.2;
  }
  46% {
    opacity: 1;
  }
  47% {
    opacity: 0.5;
  }
  48% {
    opacity: 1;
  }
}
@keyframes blink {
  40% {
    opacity: 1;
  }
  42% {
    opacity: 0.8;
  }
  43% {
    opacity: 1;
  }
  45% {
    opacity: 0.2;
  }
  46% {
    opacity: 1;
  }
  47% {
    opacity: 0.5;
  }
  48% {
    opacity: 1;
  }
}

.random--wrapper .neon--random:before,
.random--wrapper .neon--random:after {
  padding: 30px;
  color: #d4eaff;
  text-shadow: 0 0 0 transparent, 0 0 5px #3d2692,
    0 0 5px rgba(133, 190, 243, 0.5), 0 0 6px #6646d8, 0 0 5px #6244cf,
    0 0 5px #7a66c2, 0 0 5px #6046be, 0 0 5px #634bbb;
  content: attr(data-text);
  position: absolute;
  width: 100%;
  height: 100%;
  background: transparent;
  overflow: hidden;
  top: 0;
}
.random--wrapper .neon--random:before {
  left: 2px;
  text-shadow: -2px 0 red;
  animation: glitch-1 2s linear infinite reverse;
}
.random--wrapper .neon--random:after {
  left: -2px;
  text-shadow: -2px 0 blue;
  animation: glitch-2 2s linear infinite reverse;
}

