.cards {
  padding: 4rem;
  background: #020a16;
}

.neon--subhead {
  text-align: center;
  font-family: "neontubes";
}

.cards__container {
  display: flex;
  flex-flow: column;
  align-items: center;
  max-width: 1120px;
  width: 90%;
  margin: 0 auto;
}

.cards__wrapper {
  position: relative;
  margin: 50px 0 45px;
}

.cards__items {
  margin-bottom: 24px;
}

.cards__item {
  display: flex;
  flex: 1 1;
  margin: 0 1rem;
  border-radius: 10px;
  box-shadow: 0 0 0 transparent, 0 0 5px #301886, 0 0 8px #0a244e,
    0 0 10px #051630;
  transition: all 0.1s ease-in-out;
}

.cards__item__link {
  display: flex;
  flex-flow: column;
  width: 100%;
  /* box-shadow: 0 6px 20px rgba(56, 125, 255, 0.17); */
  -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
  border-radius: 10px;
  overflow: hidden;
  text-decoration: none;
}

.cards__item__pic-wrap {
  position: relative;
  width: 100%;
  padding-top: 67%;
  overflow: hidden;
}

.fade-img {
  -webkit-animation-name: fade-img;
          animation-name: fade-img;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

.cards__item__pic-wrap::after {
  content: attr(data-category);
  position: absolute;
  bottom: 0;
  margin-left: 10px;
  padding: 6px 8px;
  max-width: calc((100%) - 60px);
  font-size: 12px;
  font-weight: 700;
  color: #fff;
  background-color: #d42450;

  font-family: "Roboto", sans-serif;
}

.cards__item__img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  max-width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: all 0.2s linear;
}

.cards__item:hover .cards__item__img {
  transform: scale(1.1);
}

.cards__item:hover {
  box-shadow: 0 0 0 transparent, 0 0 5px #301886, 0 0 9px #0e2d5e,
    0 0 15px #0e2d5e, 0 0 30px #08244e;
}

.cards__item:hover .cards__item__info {
  background-color: #c6e4e4;
}

.cards__item:hover .cards__item__text {
  color: #020a16;
}

.cards__item__info {
  padding: 20px 30px 30px;
  background-color: #020a16;
  transition: all 0.1s ease-in-out;
}

.cards__item__text {
  color: #aaafbe;
  font-size: 14px;
  line-height: 24px;
  font-family: "Roboto", sans-serif;
  transition: all 0.1s ease-in-out;
}

@media only screen and (min-width: 1200px) {
  .content__blog__container {
    width: 84%;
  }
}

@media only screen and (min-width: 1024px) {
  .cards__items {
    display: flex;
  }
}

@media only screen and (max-width: 1024px) {
  .cards__item {
    margin-bottom: 2rem;
  }
}

.about--wrapper {
